*{
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
    background-color: #f8f8ff;
    height: 100%;
    
}

#root,
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie{
    width: 45%;
    background-color: #ffffff;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #b2b3bc;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,.25), 0 8px 16px -8px rgba(0,0,0,.3), 0 -6px 16px -6px rgba(0,0,0,.025);
}

.movie img{
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    position: relative;
    top: -50px;
    box-shadow: 0 30px 60px -12px rgba(50,50,93,.25), 0 18px 36px -18px rgba(0,0,0,.3), 0 -12px 36px -8px rgba(0,0,0,.025);
}

.movie .movie_title, .movie_year{
    margin: 0;
    font-weight: 300;
    margin-bottom: 5px;
}

.movie .movie_title{
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_genres{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;

}

.movie_genres li{
    margin-right: 10px;
    font-size: 14px;
}